.card{
  width:115px;
  height:115px;
  background-color: transparent;
  border: 0;
  cursor: pointer;
}
.image-card{
  width:115px;
  height:115px;
}
.front{
  width:115px;;
  height: 115px;;
  background: url('../assets/images/casino-games.png')no-repeat center center;
  background-size: cover;
  background-color:#980200;
}
.content{
  width:115px;
  height: 115px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color:#003615
}
.ReactFlipCard{
  width: 100%;
  height:100% ;
}
@media(min-width:768px){
  .card{
    width:175px;
    height: 175px;
  }
  .image-card{
    width:175px;
    height: 175px;
  }
  .front{
    width:175px;
    height: 175px;
  }
  .content{
    width:175px;
    height: 175px;
  }
}


